<template>
	<div id="order-home">
		<public-header title="我的订单"></public-header>
		<order-status-warpper v-if="order_remind && order_remind.log" :order="order_remind"></order-status-warpper>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="paotui" @click="util.jsUrl('/plugin/pages/paotui/order')" v-if="errander_status">
				<div class="image">
					<img class="img-100" src="static/img/suiyigou_icon.png">
				</div>
				<div class="headline">万能跑腿订单</div>
				<div class="icon icon-right font-12 c-gray"></div>
			</div>
			<van-list v-model="orders.loading" :finished="orders.finished" @load="onLoad" :offset="100" :immediate-check="false">
				<template v-if="orders.data.length > 0">
					<div class="order-container" v-for="(order, orderIndex) in orders.data" :key="orderIndex">
						<van-row class="top">
							<van-col span="3">
								<div class="logo" @click="util.jsUrl('/pages/store/goods', {sid: order.sid})">
									<img :src="order.logo" alt="">
								</div>
							</van-col>
							<van-col span="15" class="store">
								<div class="store-title" @click="util.jsUrl('/pages/store/goods', {sid: order.sid})">
									{{order.title}}
									<span class="icon icon-right font-12 c-gray"></span>
								</div>
								<div class="activity" v-if="order.discounts.length > 0">
									<div class="activity-item" v-for="(item, index) in order.discounts" :key="index">{{item.title}}</div>
								</div>
							</van-col>
							<van-col span="6" class="status-cn">{{order.status_text}}</van-col>
						</van-row>
						<div class="goods" @click="util.jsUrl('/pages/order/detail', {id: order.id})">
							<div class="goods-list">
								<div class="goods-item" v-for="(item, index) in order.goodsArr" @touchstart.stop="" @touchend.stop="">
									<div class="thumb">
										<img class="img-100" :src="item.thumb" alt="" />
									</div>
									<div class="goods-title">{{item.goods_title}}</div>
								</div>
							</div>
							<div class="goods-total">
								<div> <span class="font-12">
									{{Lang.dollarSign}}</span><span class="font-bold">{{order.final_fee}}</span>
								</div>
								<div class="order-num">共{{order.num}}件</div>
							</div>
						</div>
						<div class="btn-groups">
							<template  v-if="order.is_pay == 0 && order.status < 5">
								<van-button size="small" plain @click="onFinishMealPay" v-if="order.order_type == 3 && order.pay_type == 'finishMeal'" >立即支付</van-button>
								<van-button size="small" plain @click="util.jsUrl('/pages/public/pay', {order_id: order.id, order_type: 'takeout'})" v-else>立即支付</van-button>
								<van-button type="default" size="small" plain @click="util.jsUrl('/tangshi/pages/table/goods', {order_id: order.id, table_id: order.table_id, sid: order.sid})" v-if="order.order_type == 3">加菜</van-button>
							</template>
							<template v-if="order.status == 1">
								<van-button size="small" plain @click="onChangeStatus(order.id, 'cancel')">取消订单</van-button>
								<van-button type="default" size="small" plain @click="onChangeStatus(order.id, 'remind')" v-if="order.is_pay == 1">催单</van-button>
							</template>
							<template v-else-if="order.status > 1 && order.status < 5">
								<van-button size="small" plain @click="onChangeStatus(order.id, 'cancel')" v-if="order.customer_cancel_status == 1">取消订单</van-button>
								<van-button size="small" plain @click="onChangeStatus(order.id, 'end_1')" v-if="order.order_type == 1 && order.status == 4">确认送达</van-button>
								<van-button size="small" plain @click="onChangeStatus(order.id, 'end_2')" v-else-if="order.order_type == 2">我已提货</van-button>
								<van-button type="default" size="small" plain @click="onChangeStatus(order.id, 'remind')" v-if="order.is_pay == 1">催单</van-button>
								<van-button type="default" size="small" plain @click="util.jsUrl('/tangshi/pages/table/goods', {order_id: order.id, table_id: order.table_id, sid: order.sid})" v-if="order.order_type == 3 && order.is_pay == 1 && order.pay_type == 'finishMeal'">加菜</van-button>
							</template>
							<template v-else-if="order.status == 5">
								<van-button size="small" plain color="#ff2d4b"  @click="util.jsUrl('/pages/store/goods', {sid: order.sid, order_id: order.id})">再来一单</van-button>
								<van-button size="small" plain @click="util.jsUrl('/pages/order/comment', {id: order.id})" v-if="order.is_comment == 0">{{order.comment_cn}}</van-button>
								<van-button size="small" plain @click="util.jsUrl('/pages/member/comment', {})" v-if="0">查看评价</van-button>
							</template>
							<template v-else-if="order.status == 6">
								<van-button size="small" plain color="#ff2d4b" @click="util.jsUrl('/pages/store/goods', {sid: order.sid, order_id: order.id})">再来一单</van-button>
							</template>
							<van-button size="small" plain @click="util.jsUrl('/pages/order/detail', {id: order.id})" v-if="order.is_refund == 1">查看退款</van-button>
						</div>
					</div>
					<load type="loaded" text="我是有底线的" bgcolor="#f5f5f5" v-if="orders.finished"></load>
				</template>
				<van-empty description="您还没有订单，赶紧点一份！" v-else>
					<van-button round type="danger" class="bottom-button" @click="util.jsUrl('/pages/home/index')">
						现在点一份
					</van-button>
				</van-empty>
			</van-list>
		</div>
	</div>
</template>

<script>
	import PublicFooter from '@/components/footer'
	import PublicHeader from '@/components/header'
	import loading from '@/components/loading'
	import load from '@/components/load'
	import OrderStatusWarpper from '@/components/orderStatusWarpper'
	import { Dialog } from 'vant';
	export default {
		components: {
			PublicHeader,
			PublicFooter,
			load,
			OrderStatusWarpper,
			Dialog
		},
		data() {
			return {
				status: 0,
				orders: {
					min: 0,
					finished: false,
					loading: false,
					data:[]
				},
				config_mall:{},
				errander_status: false,
				showPreLoading: true,
				menufooter: {},
				order_remind: {},
			}
		},
		methods: {
			onChangeOrderStatus(name, title) {
				this.status = name;
				this.onLoad(true);
			},

			onChangeStatus(oid, type) {
				if(type == 'cancel') {
					var params = {
						url: 'wmall/order/index/cancel?id=' + oid,
						confirm: '确定取消订单吗?',
					}
				} else if(type == 'end_1' || type == 'end_2') {
					var params = {
						url: 'wmall/order/index/end?id=' + oid,
						confirm: '你确定收到该商家的外卖?',
						successUrl: '/pages/order/comment?id=' + oid,
					}
					if(type == 'end_2') {
						params.confirm = '你确定收到该商家的外卖?';
					}
				} else if(type == 'remind') {
					var params = {
						url: 'wmall/order/index/remind?id=' + oid,
					}
				}
				this.util.jspost(params);
			},
			onFinishMealPay() {
				Dialog.confirm({
					title: '温馨提示',
					message: '您的支付方式为餐后支付，请到商家收银台付款',
				}).then(() => {
					return;
				})
			},
			onLoad(force) {
				if(force === true) {
					this.orders = {
						min: 0,
						finished: false,
						loading: false,
						data:[]
					};
				}
				if(this.orders.finished) {
					return false;
				}
				this.util.request({
					url: 'wmall/order/index',
					data: {
						status: this.status,
						min: this.orders.min,
						menufooter: 1,
						order_remind: 1
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.hideLoading();
					this.orders.data = [...this.orders.data, ...result.message];
					this.orders.loading = false;
					this.orders.min = result.min;
					this.config_mall = result.config_mall;
					this.errander_status = result.errander_status;
					if(result.message.length < 20 || !result.min) {
						this.orders.finished = true;
					}
					this.menufooter = window.menufooter;
					this.order_remind = window.order;
				})
			},
			hideLoading(){
				this.showPreLoading = false;
			},
		},
		mounted() {
			this.util.jsauth();
			this.onLoad();
		}
	}
</script>

<style>
	#order-home .order-container{
		margin: 10px;
		background-color: #fff;
		border-radius: 5px;
	}
	#order-home .order-container .top{
		padding: 15px;
		display: flex;
		align-items: center;
	}
	#order-home .order-container .logo{
		position: relative;
		width: 90%;
		padding-top: 90%;
	}
	#order-home .order-container .logo img{
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	#order-home .order-container .store{
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: center;
		padding-left: 5px;
		font-weight: 500;
	}
	#order-home .order-container .store-title{
		width: 100%;
		display: flex;
		align-items: center;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
		font-weight: bold;
		line-height: 1.2;
	}
	#order-home .activity{
		position: relative;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		margin-top: 5px;
		height: 16px;
		color: #ff4b33;
		font-family: PingFangSC-Regular;
		box-sizing: border-box;
		font-size: 10px;
	}
	#order-home .activity:after,
	#order-home .activity .activity-item:after
	{
		content: '';
		pointer-events: none;
		box-sizing: border-box;
		position: absolute;
		width: 200%;
		height: 200%;
		left: 0;
		top: 0;
		transform: scale(.5);
		transform-origin: 0 0;
		border-radius: 3px;
		border: 1px solid rgba(226, 48, 40, 0.3);
	}
	#order-home .activity .activity-item{
		position: relative;
		padding: 0 4px;
	}
	#order-home .activity .activity-item:after{
		border: 0px;
		border-right: 1px solid rgba(226, 48, 40, 0.3);
	}
	#order-home .activity .activity-item:last-child:after{
		border: 0px;
	}
	#order-home .status-cn{
		color: #7a7a7a;
		font-size: 14px;
		text-align: right;
	}
	#order-home .goods{
		position: relative;
		padding: 0 15px;
	}
	#order-home .goods .goods-list{
		white-space: nowrap;
		overflow-x: scroll;
	}
	#order-home .goods .goods-item{
		width: 80px;
		display: inline-flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		margin-right: 8px;
	}
	#order-home .goods .goods-item:last-child{
		margin-right: 80px;
	}
	#order-home .goods .goods-item .thumb{
		width: 100%;
		height: 60px;
		border-radius: 5px;
		overflow: hidden;
	}
	#order-home .goods .goods-item .goods-title{
		width: 100%;
		padding: 0 5px;
		font-size: 13px;
		color: #999;
		margin-top: 5px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#order-home .goods .goods-total{
		position: absolute;
		right: 15px;
		top: 0;
		bottom: 0;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-end;
		justify-content: center;
		padding-left: 10px;
		background: linear-gradient(to right, rgba(255, 255, 255, .8), rgba(255, 255, 255, 1));
		font-size: 14px;
	}
	#order-home .goods .goods-total .order-num{
		font-size: 13px;
		color: #999;
		margin-top: 5px;
	}
	#order-home .btn-groups{
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding: 15px;
	}
	#order-home .btn-groups .van-button{
		margin-left: 10px;
		border-radius: 5px;
	}
	#order-home .paotui{
		height: 44px;
		margin: 10px;
		padding: 0 10px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-radius: 5px;
		background-color: #fff;
	}
	#order-home .paotui .image{
		width: 34px;
		height: 34px;
		font-size: 0px;
	}
	#order-home .paotui .headline{
		flex: 1;
		padding-left: 10px;
		font-size: 15px;
		font-weight: bold;
		line-height: 1.2;
	}
</style>


